.fade-enter-active,
.fade-leave-active {
  transition: opacity .3s; }

.fade-enter,
.fade-leave-to {
  opacity: 0; }

.alert {
  border: 0;
  border-radius: 0;
  color: #FFFFFF;
  padding: 20px 15px;
  font-size: 14px;
  z-index: 100;
  display: inline-block;
  position: fixed;
  transition: all 0.5s ease-in-out; }
  .alert.center {
    left: 0px;
    right: 0px;
    margin: 0 auto; }
  .alert.left {
    left: 20px; }
  .alert.right {
    right: 20px; }
  .container .alert {
    border-radius: 0px; }
  .navbar .alert {
    border-radius: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 85px;
    width: 100%;
    z-index: 3; }
  .navbar:not(.navbar-transparent) .alert {
    top: 70px; }
  .alert .alert-icon {
    font-size: 30px;
    margin-right: 5px; }
  .alert .close ~ span {
    display: inline-block;
    max-width: 89%; }
  .alert[data-notify="container"] {
    /*max-width: 400px;*/
    padding: 10px 5px 5px 10px;
    border-radius: 2px; }
  .alert.alert-with-icon {
    /*padding-left: 15px; // CK: actual left padding*/ }

.alert-info {
  background-color: #7CE4FE;
  color: #3091B2; }

.alert-success {
  background-color: #008800;
  color: #fff; }

.alert-warning {
  background-color: #e29722;
  color: #fff; }

.alert-danger {
  background-color: #FF8F5E;
  color: #B33C12; }

#flex {
  display: flex;
  justify-content: space-between; }

#flex div {
  padding: 4px; }
